@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Baloo 2", cursive;
}

body {
  background-color: #ccc;
}

header {
  background-color: #09347a;
  color: aliceblue;
  width: 100%;
  height: 10vh;
  font-size: 40px;
  padding: 2px;
  text-shadow: 2px 0 0 #609dff, -2px 0 0 #609dff, 0 2px 0 #609dff,
    0 -2px 0 #609dff, 1px 1px #609dff;
  text-align: center;
}

main {
  margin: 30px auto;
  width: 95%;
  height: 500px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 15px;
  gap: 15px;
  padding: 15px;
  background-color: #609dff;
  border: 5px groove #eee;
  overflow: scroll;
}

main::-webkit-scrollbar {
  display: none;
}

.container {
  box-shadow: 0 0 15px 0 black;
  width: 100%;
  background-color: azure;
  cursor: pointer;
  transition: 0.25s;
  border-radius: 20px;
}

.container:hover {
  box-shadow: 0 0 10px 0 black;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: 0.25s;
  background-color: yellow;
}

.card {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.img-container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.img-container img {
  width: 100%;
  max-height: 300px;
  min-height: 300px;
  border-radius: 20px 20px 0 0;
}

.title {
  font-size: 35px;
}

.calorie {
  font-size: 15px;
}

